import { Directive, ElementRef , Input  , Output, EventEmitter  } from '@angular/core';
import { CdsNavigationGroup } from '@cds/core/navigation';

@Directive({ selector: 'cds-navigation-group' })
export class CdsNavigationGroupDirective {
  protected element: CdsNavigationGroup;

  get cdsMotion() {
    return this.element.cdsMotion;
  }
  @Input() set cdsMotion(value) {
    this.element.cdsMotion = value;
  };
  get i18n() {
    return this.element.i18n;
  }
  @Input() set i18n(value) {
    this.element.i18n = value;
  };
  get expanded() {
    return this.element.expanded;
  }
  @Input() set expanded(value: boolean | '') {
    this.element.expanded = value === '' ? true : value;
  };
  get active() {
    return this.element.active;
  }
  @Input() set active(value: boolean | '') {
    this.element.active = value === '' ? true : value;
  };
  get hasFocus() {
    return this.element.hasFocus;
  }
  @Input() set hasFocus(value: boolean | '') {
    this.element.hasFocus = value === '' ? true : value;
  };

  @Output() cdsMotionChange: EventEmitter<CustomEvent> = new EventEmitter();
  @Output() expandedChange: EventEmitter<CustomEvent> = new EventEmitter();

  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
