import { Directive, ElementRef , Input   } from '@angular/core';
import { CdsNavigationStart } from '@cds/core/navigation';

@Directive({ selector: 'cds-navigation-start' })
export class CdsNavigationStartDirective {
  protected element: CdsNavigationStart;

  get i18n() {
    return this.element.i18n;
  }
  @Input() set i18n(value) {
    this.element.i18n = value;
  };
  get expandedRoot() {
    return this.element.expandedRoot;
  }
  @Input() set expandedRoot(value: boolean | '') {
    this.element.expandedRoot = value === '' ? true : value;
  };
  get hasFocus() {
    return this.element.hasFocus;
  }
  @Input() set hasFocus(value) {
    this.element.hasFocus = value;
  };
  get isGroupStart() {
    return this.element.isGroupStart;
  }
  @Input() set isGroupStart(value: boolean | '') {
    this.element.isGroupStart = value === '' ? true : value;
  };
  get navigationGroupId() {
    return this.element.navigationGroupId;
  }
  @Input() set navigationGroupId(value) {
    this.element.navigationGroupId = value;
  };
  get itemText() {
    return this.element.itemText;
  }
  @Input() set itemText(value) {
    this.element.itemText = value;
  };


  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
